<template>
    <img v-bind:style="{width: width + 'px', height: height + 'px'}"
         v-observe-visibility="onVisibilityChanged"
         v-bind:url="url" ref="img" v-on:click="$emit('click')"/>
</template>

<script>
    import {Events} from "../../../common/events";

    import Vue from 'vue'
    import VueObserveVisibility from 'vue-observe-visibility'

    Vue.use(VueObserveVisibility);

    export default {
        name: "LazyLoadImage",

        data: () => {
            return {
                isLoaded: false,
                isVisible: false
            }
        },

        props: {
            url: {
                type: String,
                required: true,
            },

            width: {
                type: Number,
            },

            height: {
                type: Number,
            }
        },

        methods: {
            callback(res) {
                //window.open(res);
                this.isLoaded = true;
                this.$refs.img.src = res;
                //console.log({url: this.url, img: this.$refs.img, res})
            },

            onVisibilityChanged(isVisible) {
                this.isVisible = isVisible;

                if (isVisible && !this.isLoaded && this.url.length !== 0) {
                    this.loadImage();
                }
            },

            loadImage() {
                this.$eventBus.$emit(Events.lazyLoadImage, {
                    url: this.url,
                    callback: this.callback,
                    priority: 1,
                });
            },
        },

        watch: {
            url: function(newVal) {
                if (this.isVisible) {
                    if (newVal.length !== 0) {
                        this.loadImage();
                    } else {
                        this.$refs.img.src = "";
                    }
                }
            }
        },

        /*computed: {
            isLoaded: function() {
                return this.$refs !== undefined && this.$refs.img !== undefined && this.$refs.img.hasAttribute("src")
            },
        }*/
    }
</script>

<style lang="sass" scoped>
    .img-ph
        background-color: red
</style>