import {CatalogAPI} from "./catalog";

export const productUpdater = {
    socketData: null,

    handshakePayload: null,

    isClosed: false,

    timer: null,

    start() {
        return new Promise((resolve, reject) => {
            try {
                this.isClosed = false;
                this.socketData = CatalogAPI.createProductsCountInStockUpdater();
                this.socketData.socket.onclose = this.onWSClose;
                this.socketData.socket.onerror = this.onWSError;
                this.socketData.socket.onopen = () => {
                    resolve();
                }
            } catch (e) {
                reject(e);
            }
        });
    },

    sendHandshake(payload) {
        return new Promise((resolve, reject) => {
            this.handshakePayload = payload;

            try {
                this.socketData.sendJSONMessage({
                    action: "HANDSHAKE",
                    payload,
                }, (event) => {
                    let data = JSON.parse(event.data);

                    if (data.status && data.status === "ok") {
                        resolve();
                    } else {
                        console.error("HANDSHAKE was unsuccessful");
                        this.close();
                        throw new Error("ba status");
                    }
                })
            } catch (e) {
                reject(e);
            }
        });
    },

    sendCustomAction(action, payload) {
        return new Promise((resolve, reject) => {
            try {
                this.socketData.sendJSONMessage({
                    action,
                    payload,
                }, (event) => {
                    let data = JSON.parse(event.data);

                    if (data.status && data.status === "ok") {
                        resolve();
                    } else {
                        console.error("HANDSHAKE was unsuccessful");
                        this.close();
                        throw new Error("ba status");
                    }
                })
            } catch (e) {
                reject(e);
            }
        });
    },

    sendGetData() {
        return new Promise((resolve, reject) => {
            try {
                this.socketData.sendJSONMessage({action: "GET"}, (event) => {
                    let data = JSON.parse(event.data);

                    if (data.status && data.status === "ok" && data.data) {
                        /*et parsedData = {};

                        for (let i = 0; i < data.data.length; i++) {
                            parsedData[this.handshakePayload[i]] = data.data[i];
                        }*/
                        resolve(data.data);
                    } else {
                        console.error("ws on GET error", {data});
                        this.close();
                        throw new Error("ws error");
                    }
                });
            } catch (e) {
                reject(e)
            }
        });
    },

    close() {
        if (this.timer) {
            clearTimeout(this.timer);
            this.timer = null;
        }

        if (this.socketData && this.socketData.socket) {
            this.socketData.socket.close();
        }
    },

    onWSError(error) {
        this.close();
        console.error("ws error", {error})
    },

    onWSClose() {
        this.isClosed = true;
        this.socketData = null;
        this.handshakePayload = null;
    },

    isWorking() {
        return this.socketData !== null && this.socketData.socket !== null
            && this.socketData.socket.readyState === 1
    },

};