<template>
    <section class="category-choose-wrapper">
        <ExpandableList v-bind:data="data"
                         @choose="onCategoryChoose" @expand="onCategoryExpand">
        </ExpandableList>
    </section>
</template>

<script>
    import ExpandableList from "../components/expandableList/ExpandableList";

    export default {
        name: "CategoryChooseComponent",

        components: {ExpandableList},

        props: {
            data: {
                required: true,
            },
        },

        methods: {
            onCategoryChoose(data) {
                this.$emit('onCategoryChoose', data);
            },

            onCategoryExpand(data) {
                this.$emit('onCategoryExpand', data);
            },
        },
    }
</script>

<style scoped lang="sass">
    .category-choose-wrapper
        width: 100%
</style>