<template>
    <section class="catalog-wrapper" v-if="error.length === 0">
        <h1 class="title">{{categoryId ? (categoryId + (subCategoryId ? (' ' + subCategoryId) : '')) : searchRequest}}</h1>

        <div v-if="products.length !== 0" class="search-result-wrapper" id="grid"
             v-bind:class="{'one-product': products.length === 1}">

            <Card v-for="item in products" :key="item.id"
                  :id="item.id" :name="item.name"
                  :options="item.options" :children-data="item.childrenData"
                  :button-text="buttonText" :basket-data="basketData"
                  v-on:buy="onBuy" v-on:showFullSizeImage="$emit('showFullSizeImage', $event)">
            </Card>

        </div>

        <div class="spinner" v-observe-visibility="onBottomReached" v-bind:style="{'margin-bottom': searchResultWrapperMarginBottom}">
            <LoadingSpinner v-if="isLoading"></LoadingSpinner>
        </div>
    </section>

    <section class="layout" v-else>
        <h1 class="title">{{error}}</h1>
    </section>
</template>

<script>
    import Card from "../components/catalog/Card";
    import LoadingSpinner from "../components/LoadingSpinner";

    import {CatalogAPI} from "../../api/catalog";
    import {INTERNAL_SERVER_ERR} from "../../common/errors";

    import {productUpdater} from "../../api/WSProductsUpdater";

    import config from "../../common/config"

    import Vue from 'vue'
    import VueObserveVisibility from 'vue-observe-visibility'
    Vue.use(VueObserveVisibility);

    import myStorage from "../../common/cache";
    import {cacheName} from "../../api/catalog"
    const cache = myStorage(cacheName);

    const itemsPerPage = 9;
    const PRODUCTS_UPDATE_TIME = 10;

    const cardMinWith = 250;

    export default {
        name: "CatalogComponent",

        components: {Card, LoadingSpinner},

        props: {
            categoryId: {
                type: String,
            },

            subCategoryId: {
                type: String,
            },

            searchRequest: {
                type: String,
            },

            buttonText: {
                type: String,
                default: "Заказать",
            },

            basketData: {
                type: Array,
                required: true
            },
        },

        data: () => {
            return {
                isLoading: true,
                hasMoreToLoad: false,

                error: "",

                lastPageNumber: 1,
                paginatedProducts: [],

                productUpdater: null,
                productUpdaterInitTimer: null,
            }
        },

        methods: {
            async init() {
                this.closeSocket();

                this.lastPageNumber = 1;
                this.paginatedProducts = [];
                this.hasMoreToLoad = false;
                this.isLoading = true;

                if (this.searchRequest) {
                    await this.search(this.searchRequest);
                } else if (this.categoryId) {
                    await this.getProducts();
                }

                this.isLoading = false;
            },

            calculateSingleCardWidth() {
                let cardsNumber = Math.round((document.querySelector(".catalog-wrapper").clientWidth / (cardMinWith + 15)) *  1000)
                    / 1000;
                console.log({cardsNumber});

                let max = (cardsNumber - (Math.floor(cardsNumber) - 1)) * cardMinWith;
                return Math.round((max - cardMinWith) / 2) + cardMinWith;
            },

            onBuy(item) {
                this.$emit('buy', item);
            },

            onBottomReached(isVisible) {
                if (!this.isLoading && this.hasMoreToLoad && this.error.length === 0 && isVisible) {
                    this.isLoading = true;
                    this.getMoreProducts()
                        .then(() => {
                            this.isLoading = false;
                        })
                }
            },

            async getProducts() {
                let payload = {
                    categoryId: this.categoryId,
                    subCategoryId: this.subCategoryId,
                    itemsPerPage
                };

                try {
                    let result = await CatalogAPI.getProductsByCategory(payload);
                    if (result !== null) {
                        let json = await result.json();
                        this.paginatedProducts.push({
                            pageNumber: 1,
                            data: json.data,
                        });
                        this.paginatedProducts.sort((a, b) => {
                            if (a.pageNumber && b.pageNumber) {
                                return a.pageNumber - b.pageNumber
                            } else {
                                return 0
                            }
                        });

                        this.hasMoreToLoad = json.hasMoreToLoad;

                        this.runProductUpdater();
                    } else {
                        console.error({result}, "while loading products");
                        this.error = INTERNAL_SERVER_ERR;
                    }
                } catch (e) {
                    console.error({e}, "while loading products");
                    this.error = INTERNAL_SERVER_ERR;
                }
            },

            async getMoreProducts() {
                this.lastPageNumber++;
                this.hasMoreToLoad = false;
                let payload = {
                    categoryId: this.categoryId,
                    subCategoryId: this.subCategoryId,
                    itemsPerPage,
                    pageNumber: this.lastPageNumber,
                };

                try {
                    let result = await CatalogAPI.getMoreProductsByCategory(payload);

                    if (result !== null) {
                        let json = await result.json();
                        this.paginatedProducts.push({
                            pageNumber: payload.pageNumber,
                            data: json.data,
                        });
                        this.paginatedProducts.sort((a, b) => {
                            if (a.pageNumber && b.pageNumber) {
                                return a.pageNumber - b.pageNumber
                            } else {
                                return 0
                            }
                        });


                        let childrenIds = [];
                        json.data.forEach((item) => {
                            childrenIds = [...childrenIds, ...Object.keys(item.childrenData)];
                        });

                        await this.productUpdater.sendCustomAction("ADD_DATA_TO_HANDSHAKE", {ids: childrenIds});
                        this.hasMoreToLoad = json.hasMoreToLoad;
                    } else {
                        console.error({result}, "while loading more products");
                        this.error = INTERNAL_SERVER_ERR;
                    }

                } catch (e) {
                    console.error({e}, "while loading more products");
                    this.error = INTERNAL_SERVER_ERR;
                }
            },

            async search() {
                try {
                    const result = await CatalogAPI.search(this.searchRequest);
                    if (result !== null) {
                        let json = await result.json();
                        this.paginatedProducts.push({
                            data: json.data,
                        });

                        this.runProductUpdater();
                    } else {
                        console.error({result}, "while search");
                        this.error = INTERNAL_SERVER_ERR;
                    }
                } catch (e) {
                    console.error({e}, "while search");
                    this.error = INTERNAL_SERVER_ERR;
                }
            },

            async runProductUpdater() {
                console.log("product updater begin");

                let downloadAndUpdateData = async () => {
                    let res = await this.productUpdater.sendGetData();

                    if (res && Object.keys(res).length > 0) {
                        for (let itemId in res) {

                            for (let i = 0; i < this.paginatedProducts.length; i++) {
                                let isFound = false;

                                for (let j = 0; j < this.paginatedProducts[i].data.length; j++) {
                                    let productWrapper = this.paginatedProducts[i].data[j];

                                    for (let prId in productWrapper.childrenData) {

                                        if (prId === itemId) {
                                            productWrapper.childrenData[prId].isCountInStockTooBig = res[itemId].isCountInStockTooBig;
                                            productWrapper.childrenData[prId].countInStock = res[itemId].countInStock;
                                            isFound = true;
                                            break
                                        }
                                    }

                                    if (isFound)
                                        break;
                                }

                                if (isFound)
                                    break;
                            }
                        }

                        await this.synchronizeWithCache();
                    }
                    this.productUpdaterInitTimer = setTimeout(() => this.runProductUpdater(), PRODUCTS_UPDATE_TIME * 1000);
                };

                this.productUpdaterInitTimer = null;
                try {
                    if (!this.productUpdater || this.productUpdater.isClosed) {
                        this.productUpdater = productUpdater;
                        await this.productUpdater.start();

                        let childrenIds = [];
                        this.products.forEach((item) => {
                            childrenIds = [...childrenIds, ...Object.keys(item.childrenData)];
                        });

                        await this.productUpdater.sendHandshake({ids: childrenIds});
                        await downloadAndUpdateData();

                    } else if (this.productUpdater && this.productUpdater.isWorking()) {
                        await downloadAndUpdateData();
                    }
                } catch (e) {
                    console.error(e);

                    if (this.productUpdater) {
                        this.productUpdater.close();
                        this.productUpdater = null;
                    }
                }
            },

            async synchronizeWithCache() {
                if (this.searchRequest) {
                    let url =  "/catalog/search",
                        params = {
                            request: this.searchRequest
                        };

                    let cacheRequest = new Request(config.PROTECTED_API_URL() + url);
                    cacheRequest.headers.append("params", encodeURI(JSON.stringify(params)));

                    let has = await cache.has(cacheRequest);
                    if (has) {
                        await cache.updateJSON(cacheRequest, {data: this.products});
                        console.log("cache was updated", {cacheRequest});
                    } else {
                        console.error("cache wasn't found", {cacheRequest})
                    }
                } else if (this.categoryId) {

                    for (let i = 0; i < this.paginatedProducts.length; i++) {
                        let item = this.paginatedProducts[i];

                        let url, params;
                        if (item.pageNumber === 1) {
                            url =  "/catalog/getProductsByCategory";
                            if (this.subCategoryId) {
                                params = {
                                    categoryId: this.categoryId,
                                    subCategoryId: this.subCategoryId,
                                    itemsPerPage
                                };
                            } else {
                                params = {
                                    categoryId: this.categoryId,
                                    itemsPerPage
                                };
                            }
                        } else {
                            url = "/catalog/getMoreProductsByCategory";

                            if (this.subCategoryId) {
                                params = {
                                    categoryId: this.categoryId,
                                    subCategoryId: this.subCategoryId,
                                    pageNumber: item.pageNumber,
                                    itemsPerPage
                                };
                            } else {
                                params = {
                                    categoryId: this.categoryId,
                                    pageNumber: item.pageNumber,
                                    itemsPerPage
                                };
                            }
                        }

                        let cacheRequest = new Request(config.PROTECTED_API_URL() + url);
                        cacheRequest.headers.append("params", encodeURI(JSON.stringify(params)));

                        let has = await cache.has(cacheRequest);
                        if (has) {
                            let res = [];
                            item.data.forEach(t => {
                                res.push(t);
                            });

                            await cache.updateJSON(cacheRequest, {
                                data: res,
                                hasMoreToLoad: (i === this.paginatedProducts.length - 1) ? this.hasMoreToLoad : true
                            });
                            console.log("cache was updated", {cacheRequest, res});
                        } else {
                            console.error("cache wasn't found", {cacheRequest})
                        }
                    }
                }
            },

            closeSocket() {

                if (this.productUpdater) {
                    this.productUpdater.close();
                }

                if (this.productUpdaterInitTimer) {
                    clearTimeout(this.productUpdaterInitTimer);

                }

                this.productUpdater = null;
                this.productUpdaterInitTimer =  null;
            },
        },

        computed: {
            products: function() {
                let res = [];
                this.paginatedProducts.forEach(item => {
                    if (item.data) {
                        res.push(...item.data);
                    }
                });
                return res;
            },

            searchResultWrapperMarginBottom: function() {
                /*if (this.isBaskeWindowVisible) {
                    return 230 + "px"
                } else {
                    return 0 + "px"
                }*/

                return 230 + "px"
            },

        },

        mounted() {
            this.init();
        },

        beforeDestroy() {
            this.closeSocket();
        },
    }
</script>

<style scoped lang="sass">
    @import '../../assets/basic'

    .one-product
        display: flex!important
        justify-content: center!important

        .card
            width: 280px!important

    .catalog-wrapper
        font-family: Roboto, sans-serif

        .title
            margin-top: 1rem
            text-align: center
            font-size: 1.8rem
            font-weight: normal
            font-family: Oswald, sans-serif

        .spinner
            display: flex
            justify-content: center
            margin-top: 2rem


        .search-result-wrapper
            display: grid
            grid-gap: 15px
            grid-template-columns: repeat( auto-fill, minmax(250px, 1fr) )

    /*@include mobile
        grid-template-columns: 1fr

    /*@include tablet
        grid-template-columns: repeat(2, 1fr)

    /*@include laptop
        grid-template-columns: repeat(3, 1fr)

    /*@include laptop-l
        grid-template-columns: repeat(4, 1fr)

</style>