<template>
    <div class="droplist-container" @mouseup="isVisible = true" @mouseleave="isVisible = false">
        <p class="trigger">
            {{$props.data[chosenIndex]}}
            <!--<i v-if="!isVisible" class="fas fa-caret-down"></i>
            <i v-else class="fas fa-caret-up"></i>-->
            <img v-if="!isVisible" src="/svgs/caret-down.svg"/>
            <img v-else src="/svgs/caret-up.svg"/>
        </p>

        <div class="content" v-if="isVisible">
            <p v-for="(item) in dataToRender" :key="item"
               v-on:click="clickHandler(item)">{{item}}</p>
        </div>
    </div>
</template>

<script>
    export default {
        name: "SimpleDroplist",

        props: {
            data: {
                type: Array,
                required: true,
            },

            chosenIndex : {
                type: Number,
                required: true
            }
        },

        data: () => {
            return {
                isVisible: false,
            }
        },

        methods: {
            clickHandler(item) {
                for (let i = 0; i < this.$props.data.length; i++) {
                    if (item === this.$props.data[i]) {
                        this.$emit("choose", i);
                        this.isVisible = false;
                        return;
                    }
                }
            },
        },
        
        computed: {
            dataToRender: function () {
                /*let res = [];

                if (this.$props && this.$props.data) {
                    this.$props.data.forEach((element) => {
                        if (this.$props.data[this.chosenIndex] !== element) {
                            res.push(element);
                        }
                    });
                }*/

                return this.data;
            }
        },

        watch: {
            data: function(newVal, oldVal) {
                for (let i = 0; i < newVal.length; i++) {
                    if (newVal[i] === oldVal[this.chosenIndex]) {
                        if (this.chosenIndex !== i) {
                            this.$emit("choose", i);
                        }
                        return
                    }
                }

                this.$emit("choose", 0);
            }
        }
    }
</script>

<style lang="sass" scoped>
    @import '../../../assets/basic'

    .droplist-container
        position: relative
        padding-right: 10px
        display: block
        font-family: Roboto, sans-serif

        .trigger
            margin: 0
            text-align: center
            cursor: pointer
            color: #777777
            font-size: 0.9em

            img
                width: 12px
                height: 12px

        .content
            position: absolute
            width: 150px
            background-color: $background-color
            box-shadow: 0 1px 19px rgba(0, 0, 0, 0.5)
            border-radius: 5px
            z-index: 1
            left: 50%
            -webkit-transform: translateX(-50%)
            transform: translateX(-50%)

            p
                margin: 0
                padding-top: 0.7rem
                padding-bottom: 0.7rem
                padding-left: 10px
                cursor: pointer
                transition: 0.2s

                &:first-child
                    border-top-right-radius: 5px
                    border-top-left-radius: 5px

                &:last-child
                    border-bottom-right-radius: 5px
                    border-bottom-left-radius: 5px

                &:hover
                    background-color: #f2f2f2

</style>